.block__row {
	border: 10px solid #036149
}

.block__column {
	border: 10px solid #adcecf;
}

.block__item {
	background-color: #333333;
	padding: 10px 25px;
	font-size: 20px;
	color: #fff;
	font-weight: 700;
}
.block__item_big {
	padding: 25px 25px;
}

.flex_cont {
	display: flex;
}
.flex_col {
	flex-direction: column;
}

.in_flex_cont {
	display: inline-flex;
}

.jcs {
	justify-content: flex-start;
}

.jce {
	justify-content: flex-end;
}

.jcc {
	justify-content: center;
}
.jcsb {
	justify-content: space-between;
}
.jcsa {
	justify-content: space-around;
}
.alignstr {
	align-items: stretch;
}
.aligns {
	align-items: flex-start;
}
.aligne {
	align-items: flex-end;
}
.alignc {
	align-items: center;
}
.fwrap {
	flex-wrap: wrap;
}
.fwraprew {
	flex-wrap: wrap-reverse;
}

.anim_wrap {
	animation-name: wrap;
	animation-duration: 1s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: 4;
	animation-direction: alternate;
}

@keyframes wrap {
	0% {
		width: 100%;
	}
	100% {
		width: 20%;
	}
}

