*,*::before,*::after {
	padding: 0;
	margin: 0;
	border: 0;
	box-sizing: border-box;
}

pre {
	margin: 0;
}

html, body {
	height: 100%;
}

body {
	background-color: #000;
}

.root {
	min-height: 100vh;
}

.container.bg-color {
	position: relative;
	background-color: #7d7b75;
	min-height: 100vh;
}