.header.navbar {
	background-color: #333333;
	display: block;
	position: static;
}
.header__container {
	padding: 0px 20px;
}
.header__body {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	height: 50px;
}
.header__logo {
	color: #fff;
	text-decoration: none;
	font-size: 20px;
	font-weight: normal;
	font-family: "Cabin Sketch";
	margin-right: 20px;
}
.header__logo:hover {
	color: #fff;
}
.header__burger {
	display: none;
}
.header__list {
	display: flex;
	justify-content: space-between;
	padding: 0;
	margin: 0;
}
.header__list li{
	list-style: none;
	margin: 0 0 0 50px;
}
.header__link {
	color: #fff;
	text-decoration: none;
	font-family: "Cabin Sketch"
}
.header__link:hover {
	color: #fff;
}

@media (max-width: 767px) {

	.header__body {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 40px;
	}
	.header__burger {
		display: block;
		background-color: #212529;
		position: relative;
		width: 40px;
		height: 30px;
		border-radius: 45%;
		box-shadow: 0 0 5px #fff;
		transition: all 0.3s ease;
	}
	.header__burger span {
		background-color: #fff;
		position: absolute;
		width: 80%;
		height: 2px;
		left: 4px;
		top: 14px;
		transition: all 0.1s linear;
	}
	.header__burger::before,
	.header__burger::after {
		content: '';
		background-color: #fff;
		position: absolute;
		width: 60%;
		height: 2px;
		left: 8px;
		transition: all 0.1s linear;
	}
	.header__burger::before {
		top: 7px;
	}
	.header__burger::after {
		bottom: 7px;
	}
	.header__burger.active {
		outline: 3px solid #fff;
		outline-offset: 2px;
		box-shadow: none;
	}
	.header__burger.active::before {
		transform: rotate(45deg);
		top: 14px;
	}
	.header__burger.active::after {
		transform: rotate(-45deg);
		bottom: 14px;
	}
	.header__burger.active span {
		transform: scale(0);
	}

	.header__menu {
		position: absolute;
		top: 70px;
		right: -6%;
		width: 12%;
		height: 80%;
		background-color: #333333;
		padding: 70px 10px 20px 10px;
		transition: all 0.3s ease-out;
		overflow: auto;
		border: 2px solid #212529;
		border-top-left-radius: 30px;
		border-bottom-left-radius: 5px;
		transform: scaleX(0);
		z-index: 50;
	}
	.header__menu.active {
		right: 0;
		transform: scaleX(1);
	}
	.header__list {
		display: block;
	}
	.header__list li {
		text-align: center;
		margin: 0 0 20px 0;
	}
	.header__link {
		font-size: 12px;
	}
}